.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    background-color: rgba(0, 0, 0, .5);
    justify-content: center;
    opacity: 0;
    display: flex;
    visibility: hidden;
    transition: opacity 0.8s, visibility linear 0.3s;      /*в firefox все плавно открывается и закрывается*/
    transition-delay: 0s, 0s;
}