.header__logout {
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  border: none;
  outline: none;
  background: inherit;
  color: #A9A9A9;
}

.header__logout:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 374px) {
  .header__logout {
    margin-top: 18px;
  }
}