.authorization__submit {
  margin-top: 186px;
  margin-bottom: 15px;
  padding: 14px 0;
  height: 50px;
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  border: none;
  border-radius: 2px;
  color: #000000;
  background: #ffffff;
}

.authorization__submit:hover {
  opacity: 0.85;
  cursor: pointer;
}

@media screen and (max-width: 374px) {
  .authorization__submit {
    margin-top: 143px;
    font-size: 16px;
    line-height: 19px;
    padding-bottom: 20px;
    height: 46px;
  }
}