.popup__tooltip-message {
  text-align: center;
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 60px; 
}

@media screen and (max-width: 374px) {
  .popup__tooltip-message {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 50px;
  }
}